  cursor: pointer;
  border-radius: 100%;
}

.intro_dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  border-radius: 100%;
}

.intro_dots li button:hover,
.intro_dots li button:focus {
  outline: none;
}

.intro_dots li button:hover:before,
.intro_dots li button:focus:before {
  opacity: 1;
}

.intro_dots li button:before {
  font-size: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  content: '';
  text-align: center;
  background-color: transparent;
  border: 2px solid #9E9E9E;
  border-radius: 100%;
}

.intro_dots li.slick-active button:before {
  border-color: #009900;
